.meme-item {
  margin: 1rem 0;
  box-shadow: 0 2px 8px rgba(0, 0, 0, 0.26);
  border-radius: 6px;
  padding: 1rem;
}

.meme-item h2 {
  margin: 0 0 0.5rem 0;
  font-size: 1.75rem;
  color: #343434;
}

.meme-item p {
  margin: 0;
  color: #505050;
}

.meme-item img {
  width: 100%;
  height: 100%;
}


